import { FETCH_SUBRUBROS_REQUEST, FETCH_SUBRUBROS_SUCCESS, FETCH_SUBRUBROS_ERROR } from '../actions/subRubrosActions'

const initialState = {
  array: [],
  isFetching: false
};

function getSubRubrosReducers(state = initialState, action) {
  switch (action.type) {
    case FETCH_SUBRUBROS_REQUEST:
      return {
        ...state,
        isFetching: true
      }
    case FETCH_SUBRUBROS_SUCCESS:
      return {
        ...state,
        isFetching: false,
        array: action.payload
      }
    case FETCH_SUBRUBROS_ERROR:
      return {
        ...state,
        isFetching: false,
      }
    default:
      return state;
  }
}

export default getSubRubrosReducers;
