import {
  REGISTER_USER_REQUEST,
  REGISTER_USER_SUCCESS,
  REGISTER_USER_ERROR,
} from "../actions/registerActions";

const initialState = {
  loggedIn: false,
  array: [],
  isFetching: false,
};

function registerUserReducers(state = initialState, action) {
  switch (action.type) {
    case REGISTER_USER_REQUEST:
      return {
        ...state,
        loggedIn: false,
        isFetching: true,
      };
    case REGISTER_USER_SUCCESS:
      return {
        ...state,
        loggedIn: true,
        isFetching: false,
        array: action.payload,
      };
    case REGISTER_USER_ERROR:
      return {
        ...state,
        loggedIn: false,
        isFetching: false,
      };
    default:
      return state;
  }
}

export default registerUserReducers;
