import { FETCH_PRODUCTOS_DETAIL_REQUEST, FETCH_PRODUCTOS_DETAIL_SUCCESS, FETCH_PRODUCTOS_DETAIL_ERROR } from '../actions/productosDetailActions'

const initialState = {
  array: [],
  isFetching: false
};

function getProductosDetailReducers(state = initialState, action) {
  switch (action.type) {
    case FETCH_PRODUCTOS_DETAIL_REQUEST:
      return {
        ...state,
        isFetching: true
      }
    case FETCH_PRODUCTOS_DETAIL_SUCCESS:
      return {
        ...state,
        isFetching: false,
        array: action.payload
      }
    case FETCH_PRODUCTOS_DETAIL_ERROR:
      return {
        ...state,
        isFetching: false,
      }
    default:
      return state;
  }
}

export default getProductosDetailReducers;
