import axios from "axios";

export const FETCH_SUBRUBROS_REQUEST = "FETCH_SUBRUBROS_REQUEST";
export const FETCH_SUBRUBROS_SUCCESS = "FETCH_SUBRUBROS_SUCCESS";
export const FETCH_SUBRUBROS_ERROR = "FETCH_SUBRUBROS_ERROR";

const getSubRubrosActions = (dispatch) => {
  dispatch({ type: FETCH_SUBRUBROS_REQUEST });

  axios
    .get("https://www.sundelimpieza.com/api/productos/subrubro") // https://www.sundelimpieza.com/api/
    .then(res => {
      dispatch({
        type: FETCH_SUBRUBROS_SUCCESS,
        payload: res.data
      });
    })
    .catch(error => {
      dispatch({
        type: FETCH_SUBRUBROS_ERROR,
        erorr: error.toString()
      });
    });
};

export default getSubRubrosActions;