import axios from "axios";

export const FETCH_PRODUCTOS_DETAIL_REQUEST = "FETCH_PRODUCTOS_DETAIL_REQUEST";
export const FETCH_PRODUCTOS_DETAIL_SUCCESS = "FETCH_PRODUCTOS_DETAIL_SUCCESS";
export const FETCH_PRODUCTOS_DETAIL_ERROR = "FETCH_PRODUCTOS_DETAIL_ERROR";

const getProductosDetailActions = (detail) => (dispatch) => {
  dispatch({ type: FETCH_PRODUCTOS_DETAIL_REQUEST });

  axios
    .get(`https://www.sundelimpieza.com/api/productos/detalle/${detail}`) // https://www.sundelimpieza.com/api/
    .then(res => {
      dispatch({
        type: FETCH_PRODUCTOS_DETAIL_SUCCESS,
        payload: res.data
      });
    })
    .catch(error => {
      dispatch({
        type: FETCH_PRODUCTOS_DETAIL_ERROR,
        erorr: error.toString()
      });
    });
};

export default getProductosDetailActions;