import axios from "axios";

//import getUserActions from "./usersActions";

export const REGISTER_USER_REQUEST = "REGISTER_USER_REQUEST";
export const REGISTER_USER_SUCCESS = "REGISTER_USER_SUCCESS";
export const REGISTER_USER_ERROR = "REGISTER_USER_ERROR";

const registerUserActions = (user) => (dispatch) => {
  dispatch({ type: REGISTER_USER_REQUEST });

  axios
    .post("https://www.sundelimpieza.com/api/usuarios/register", user) // https://www.sundelimpieza.com/api/
    .then((res) => {
      
      dispatch({
        type: REGISTER_USER_SUCCESS,
        payload: res.data,
      });

    })
    .catch((err) => {
      dispatch({
        type: REGISTER_USER_ERROR,
        payload: err.data,
      });
    })
    /* .finally(() => {
      dispatch(getUserActions);
    }); */
};

export default registerUserActions;
